import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { motion } from 'framer-motion';
import moment from 'moment';
import 'moment/locale/es'

import homeBanner from '../../assets/homeBanner.svg';
import timeIcon from '../../assets/timeIcon.svg';
import dateIcon from '../../assets/dateIcon.svg';
import enterpriseIconCard from '../../assets/enterpriseIconCard.svg';
import locationIconCard from '../../assets/locationIconCard.svg';
import emailIconCard from '../../assets/emailIconCard.svg';
import { startEnterpriseLoad } from '../../actions/enterprise';
import { onView } from '../../actions/ui';
import './home.css';
import { clearSearchingClients } from '../../actions/clients';
import { startClearInvoiceActive } from '../../actions/invoice';

const HomeScreen = () => {

    moment.locale('es');

    const dispatch = useDispatch();

    const { active: code } = useSelector( state => state.auth );
    const { myEnterprise } = useSelector( state => state.enterprise );
    const [hour, setHour] = useState(moment().format('LTS'));

    useEffect(() => {
        localStorage.setItem('thisPage', 'inicio');
        dispatch( onView( localStorage.getItem('thisPage') ) );
        dispatch( clearSearchingClients() );
        dispatch( startClearInvoiceActive() );
    }, [dispatch]);

    useEffect(() => {
        const interval = setInterval(() => {
            setHour(moment().format('LTS'));
        }, 1000);

        return () => clearInterval(interval);
    }, []);
    
    !myEnterprise && dispatch( startEnterpriseLoad( code ) );
    
    return (
        <div className='home'>
            <div className='container-banner-home'>
                <motion.div
                    className='banner-home'
                    initial={{ scale: 0.8 }}
                    animate={{ scale: 1 }}
                >
                    <div className='banner-info'>
                        {
                            myEnterprise
                                ? <p className='banner-title'>{ myEnterprise.nombre.trim() }</p>
                                : ''
                        }
                        <div className='banner-hour'>
                            <img src={timeIcon} alt='home-banner' />
                            <p className='hour'>{hour}</p>
                        </div>

                        <div className='banner-date'>
                            <img src={dateIcon} alt='home-banner' />
                            <p className='date'>{moment().format('LL')}</p>
                        </div>
                    </div>
                    
                    <img className='banner-img' src={homeBanner} alt='banner' />
                </motion.div>
            </div>

            <div className='cards-home'>
                <motion.div
                    className='container-home ruc'
                    initial={{ scale: 0.8 }}
                    animate={{ scale: 1 }}
                >
                    <div className='glass-card'>
                        <div className='icon-info'>
                            <img src={enterpriseIconCard} alt='ícono' />
                        </div>
                        {
                            myEnterprise 
                                ? <p className='card-info info-ruc'>{ myEnterprise.ruc.trim() }</p>
                                : ''
                        }
                    </div>
                </motion.div>

                <motion.div
                    className='container-home direccion'
                    initial={{ scale: 0.8 }}
                    animate={{ scale: 1 }}
                >   
                    <div className='glass-card'>
                        <div className='icon-info'>
                            <img src={locationIconCard} alt='ícono' />
                        </div>
                        {
                            myEnterprise 
                                ? <p className='card-info info-direccion'>{ myEnterprise.direccion.trim() }</p>
                                : ''
                        }
                    </div>
                </motion.div>

                <motion.div
                    className='container-home correo'
                    initial={{ scale: 0.8 }}
                    animate={{ scale: 1 }}
                >
                    <div className='glass-card'>
                        <div className='icon-info'>
                            <img src={emailIconCard} alt='ícono' />
                        </div>
                        {
                            myEnterprise 
                                ? <p className='card-info info-email'>{ myEnterprise.email.trim() }</p>
                                : ''
                        }
                    </div>
                </motion.div>
            </div>

        </div>
    )
}

export default HomeScreen;