import Swal from 'sweetalert2';

import { fetchConToken } from '../helpers/fetch';
import { types } from '../types/types';

export const startEnterpriseLoad = ( code ) => {
    return async( dispatch ) => {

        const userEncrypt = localStorage.getItem( 'p1');
        const passwordEncrypt = localStorage.getItem( 'p2');
        const databaseEncrypt = localStorage.getItem( 'p3');

        const resp = await fetchConToken( 'auth/miempresa', { 
                userEncrypt, 
                passwordEncrypt, 
                databaseEncrypt,
                code }, 
                'POST' );
        const body = await resp.json();
        
        if( body.ok ){
            dispatch( enterpriseLoad({
                myEnterprise: body.msg
            } ) );
        }else{
            Swal.fire({
                title: 'Error',
                text: body.msg,
                icon: 'error',
                confirmButtonText: 'Ok',
                confirmButtonColor: "#0191CE"
            });
        }

    }
}

const enterpriseLoad = ( myEnterprise ) => ({
    type: types.empresaLoad,
    payload: myEnterprise
});